import React,{Component} from 'react'

class MemeGenerator extends React.Component {	
	constructor(){
		super()
		this.state = {
			topText:"",
			bottomText:"",
			randomImage:"https://i.imgflip.com/1bij.jpg",
			allMemeImages:""
		}
		this.handleChange = this.handleChange.bind(this)
		this.handleSubmit = this.handleSubmit.bind(this) 
	}

	componentDidMount(){
		fetch("https://api.imgflip.com/get_memes").then(response => response.json()).then(response => {
			const {memes} = response.data
			//console.log(memes[0])
			this.setState({allMemeImages: memes})
		})


	}

	handleChange(event){
		//console.log("Working!")
		const {name,value} = event.target //this line makes sure we see the text we are typing into the box
		this.setState({[name]: value})
	}

	handleSubmit(event){
		event.preventDefault()
		const randNum = Math.floor(Math.random() * this.state.allMemeImages.length)
		const randNameImg = this.state.allMemeImages[randNum].url
		this.setState({randomImage: randNameImg})
	}



	render(){
		return(
			<div>
				
				<form className="meme-form" onSubmit={this.handleSubmit}>	
					<input type="text" name="topText" placeholder="Top Text" value={this.state.topText} onChange={this.handleChange}/>
					<input type="text" name="bottomText" placeholder="Bottom Text" value={this.state.bottomText} onChange={this.handleChange}/>
					<button>Generate</button>
				</form>

				<div className="meme">
					<img src={this.state.randomImage} alt="meme" />
					<h2 className="top">{this.state.topText}</h2>
					<h2 className="bottom">{this.state.bottomText}</h2>
				</div>
			
			</div>
		)
	}
}

export default MemeGenerator;